p {
  margin: 0;
}

li {
  display: grid;
  grid-template-columns: auto 1fr auto auto;
  gap: 12px;
  align-items: center;
  padding: 0px 10px;
  border-radius: 8px;
  cursor: default;
  font-size: 17px;
  /* margin: 4px 0px; */
  padding: 6px;

  user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.task-completed {
  text-decoration: line-through;
  text-decoration-color: inherit;
}

.task-incompleted {
  text-decoration: none;
}

li img {
  cursor: pointer;
}

.edit-button {
  width: 20px;
}

.delete-button {
  width: 20px;
  cursor: pointer;
}

@media (max-width: 425px) {
  li img {
    width: 24px;
    height: 24px;
  }

  .edit-button {
    width: 17px;
    height: 17px;
  }
}
@media (max-width: 425px) {
  li p {
    font-size: 15px;
  }
}
