.input-container {
  display: flex;
  justify-content: center;
}

.input {
  background-image: url("data:image/svg+xml;base64,");
  background-size: 24px;
  background-position: right;
  background-repeat: no-repeat;
  background-origin: content-box;
  width: 280px;
  height: 35px;
  border-radius: 6px;
  outline: none;
  text-align: start;
  border-style: none;
  color: gray;
  font-family: "Istok Web", sans-serif;
  font-size: 18px;
  padding: 0px 10px;
}

.input::placeholder {
  text-align: start;
}

.input:disabled {
  opacity: 25%;
}
