.header-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-container h1 {
  font-size: 42px;
  background: -webkit-linear-gradient(rgb(255, 230, 0), rgb(255, 118, 6));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  cursor: default;
  margin: 0px;
}

.header-container img {
  width: 60px;
  height: 60px;
}
